import React, { Component } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import Link from 'valuelink';
import { Input } from 'valuelink/lib/tags';
import logotms from "../../images/name.png";
import { loginUser } from '../../actions/index';
import box_pattern from '../../images/onboarding/box_pattern.png';
import { PrimaryButton } from '../../components/common/Buttons';

const OuterContainer = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  justify-content: center;
`;

const TitleContainer = styled.div`
  position: relative;
  overflow: visible;
  z-index: 1;
  margin-left: 0.625rem;
  margin-right: 0.625rem;
`;

const TitleText = styled.p`
  font-size: 1.5rem;
  text-align: center;
  color: #47464a;
  margin-left: 0.625rem;
  margin-bottom: 1.625rem;
  margin-right: 0.625rem;
`;

const InputContainer = styled.div`
  background-color: #f8f8f8;
  padding: 0.68rem 0;
  width: 254px;
  border-radius: 6px;
  display: flex;
  /* align-self: stretch; */
  align-self: center;
  align-items: center;
  margin-bottom: 0.625rem;
  /* margin-left: 1.625rem;
  margin-right: 1.625rem; */
`;

const StyledButtonText = styled.p`
  color: white;
  flex: 0;
  /* text-transform: uppercase; */
  margin: 0;
  text-align: center;
`;

const StyledInputBox = styled(Input)`
  background-color: #f8f8f8;
  text-align: center;
  /* margin-right: 1.875rem; */
  flex: 1;
  width: 205px;
`;

const SubmitButton = styled(PrimaryButton)`
  align-self: center;
  justify-content: center;
  align-items: center;
  margin-top: 1.25rem;
  width: 300px;
`;

class LoginScreen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userName: '',
      password: '',
      loading: false,
    };
  }

  handleKeyPress = (event) => {
    // console.log({ event, focus: document.activeElement.id });
    if (event.code === 'Enter') {
      this.login();
    }
  };

  componentDidMount = () => {
    window.addEventListener('keydown', this.handleKeyPress, false);
  };

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleKeyPress, false);
  }

  componentWillUpdate = () => {
    // otp error then change loading back to false
    if (this.state.loading && this.props.appError.hasError) {
      this.setState({
        loading: false,
      });
    }
  };

  login = () => {
    this.setState({
      loading: true,
    });
    console.log('Phone number submitted ', this.state.userName);

    // login
    const { dispatch, history } = this.props;
    const { userName, password } = this.state;
    dispatch(loginUser(userName, password, history));
  };

  render() {
    const userNameLink = Link.state(this, 'userName');
    const passwordLink = Link.state(this, 'password');
    return (
      <OuterContainer>
        <TitleContainer>
          <img style={{marginBottom: 20}} alt="tms" src={logotms} />
          <TitleText>Kindly login to your account</TitleText>
        </TitleContainer>
        <InputContainer>
          <StyledInputBox
            className="tms-input"
            maxLength={30}
            valueLink={userNameLink}
            placeholder="User Name"
          />
        </InputContainer>
        <InputContainer>
          <StyledInputBox
            className="tms-input"
            valueLink={passwordLink}
            placeholder="Password"
            type="password"
          />
        </InputContainer>
        <SubmitButton onTap={this.login} loading={this.state.loading}>
          <StyledButtonText>Continue</StyledButtonText>
        </SubmitButton>
      </OuterContainer>
    );
  }
}

const mapStateToProps = (state) => ({
  appError: state.app.appError,
});

export default connect(mapStateToProps)(LoginScreen);
