import React, { Component } from "react";
import { Modal, ModalBody } from "reactstrap";
import styled from "styled-components";
import { FaTimes } from "react-icons/fa";
import StyledTappable from "./StyledTappable";

const CloseModal = styled(FaTimes)``;

const HeaderComponent = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1px 10px;
`;
const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

class ResultModal extends Component {
  render() {
    return (
      <Modal isOpen toggle={this.props.closeModal} style={customStyles}>
        <HeaderComponent>
          <h3 className={this.props.textColor} style={{ paddingTop: 5 }}>
            {this.props.modalHeader}
          </h3>
          <StyledTappable onTap={this.props.closeModal}>
            <CloseModal />
          </StyledTappable>
        </HeaderComponent>
        <ModalBody>{this.props.modalBody}</ModalBody>
      </Modal>
    );
  }
}

export default ResultModal;
