export const defaultState = {
  id: null,
  authToken: null,
  createdAt: null,
  division: null,
  phone_number: null,
  railway: null,
  station: null,
  userId: null,
  user_name: null,
};

export default function user(state = defaultState, action) {
  switch (action.type) {
    case 'SET_USER_INFO':
      return Object.assign({}, state, action.payload.operatorDetails);
    case 'AUTH_TOKEN':
      return Object.assign({}, state, {
        authToken: action.payload,
      });
    case 'LOGOUT_USER':
      return Object.assign(
        {},
        {
          defaultState,
        }
      );
    default:
      return state;
  }
}
