import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { Layout, Menu, Avatar, Popconfirm } from "antd";
import logotms from "../../images/name.png";
import { logoutUser } from "../../actions/index";
import { debounce } from "../../utils";
import userAvatar from "../../images/avatar.png";
import styled from "styled-components";

const StyledLayout = styled(Layout)`
  display: flex;
  flex-direction: column;
  flex: 1;
`;

const StyledHeader = styled.div`
  height: 60px;
  display: flex;
  flex-direction: row;
  background: #ffffff;
`;
const SubLayout = styled(Layout)`
  display: flex;
  flex-direction: row;
  flex: 1;
`;

const SideBar = styled.div`
  width: 200px;
  display: flex;
  flex-direction: column;
  background-color: #011529;
`;

const Content = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow: scroll;
`;


const styles = {
  sider: {
    position: "absolute",
    // width: "20%",
    minHeight: "97.207%",
    background: "#011529",
  },
  tmsLogo: {
    // position: 'absolute',
    height: "4rem",
    marginLeft: "13px",
    marginTop: "0px",
    // right: 0,
    // bottom: 0,
    width: "37.5rem",
  },
  menu: {
    borderRight: 0,
    backgroundColor: "#011529",
  },
  menuItem: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
  },
  input: {
    height: 60,
    paddingLeft: 60,
    background: "#FFFFFF",
  },
  header: {
    position: "absolute",
    minWidth: "1240px",
    width: "90%",
    height: "60px",
    left: "200px",
    top: "0px",

    background: "#FFFFFF",
  },
  userName: {
    position: "absolute",
    // width: '115px',
    // height: '17px',
    right: "160px",
    fontStyle: "normal",
    fontWeight: "500",
    textalign: "right",
    letterspacing: "0.01em",
    color: "#A6ACBE",
  },
  userPost: {
    position: "absolute",
    // width: '71px',
    height: "15px",
    right: "160px",
    top: "33px",

    fontFamily: "Inter",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: "12px",
    lineHeight: "15px",
    textAlign: "right",
    letterSpacing: "0.01em",

    color: "#A6ACBE",
  },
  avatar: {
    position: "absolute",
    width: "50px",
    height: "50px",
    right: "100px",
    top: "5px",
  },
};
class LayoutContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
      selectedMenu: props.location.pathname.split("/")[1],
      userAvatar: "",
      userName: props.user.user_name,
    };
    // console.log({ props });
    if (!props.authToken) {
      props.history.push("/");
    }
    this.debounceSearch = debounce((val) => this.props.searchFun(val), 800);
  }
  componentDidMount = () => {
    const user = this.props.user.user_name;
    if (user === "Rohit") {
      this.setState({
        showadmin: true,
      });
    }
    if (user === "nayanagg") {
      this.setState({ showadmin: true });
    }
  };
  onSearchTextChange = (e) => {
    if (e.target.value.length > 2 || e.target.value.length === 0) {
      this.debounceSearch(e.target.value);
    }
  };
  logoutUser = () => {
    const { dispatch, history } = this.props;
    dispatch(logoutUser(history));
    this.props.history.push("/");
  };
  selectMenu = (options) => {
    this.setState({
      selectedMenu: options.key,
    });
    this.getContent(options.key);
  };
  render() {
    const { history } = this.props;
    return (
      <StyledLayout>
        <StyledHeader className="site-layout-background">
          <div className="logo">
            <img style={styles.tmsLogo} alt="tms" src={logotms} />
          </div>
          <Popconfirm
            title="Are you sure you want to logout?"
            placement="bottom"
            // icon={null}
            onConfirm={this.logoutUser}
            cancelText="No"
            okText="Logout"
          >
            <Avatar
              style={styles.avatar}
              icon={<img alt="avatar" src={userAvatar} />}
            />
          </Popconfirm>
        </StyledHeader>
        <SubLayout className="site-layout">
          <SideBar>
            <Menu
              mode="inline"
              defaultSelectedKeys={["users"]}
              selectedKeys={[this.state.selectedMenu]}
              theme="dark"
              style={styles.menu}
            >
              <Menu.Item onClick={() => history.push("/home")} key="home">
                Home
              </Menu.Item>
              <Menu.Item
                onClick={() => history.push("/account-details")}
                key="account-details"
              >
                User Details
              </Menu.Item>
              {this.state.showadmin && (
                <Menu.Item
                  onClick={() => history.push("/add-users")}
                  key="add-users"
                >
                  Add Users
                </Menu.Item>
              )}
              {this.state.showadmin && (
                <Menu.Item
                  onClick={() => history.push("/all-users")}
                  key="all-users"
                >
                  All Users
                </Menu.Item>
              )}
              <Menu.Item
                onClick={() => history.push("/crt-devices")}
                key="crt-devices"
              >
                CRT Devices
              </Menu.Item>
              <Menu.Item
                onClick={() => history.push("/sensor-report")}
                key="sensor-report"
              >
                Sensor Report
              </Menu.Item>
              <Menu.Item
                onClick={() => history.push("/IFD-connect-history")}
                key="IFD-connect-history"
              >
                IFD Connect History
              </Menu.Item>
            </Menu>
          </SideBar>
          <Content>{this.props.children}</Content>
        </SubLayout>
      </StyledLayout>
    );
  }
}

const mapStateToProps = (state) => ({
  authToken: state.user.authToken,
  user: state.user,
});

export default withRouter(connect(mapStateToProps)(LayoutContainer));
