import baseRequest from "./baseRequest";

export const getUserDetails = (userName) => {
  const url = "/crtweb/show-user-details";
  const method = "get";
  const params = {
    userName: userName,
  };
  return baseRequest(url, method, params, null);
};
export const updateUserDetails = (
  emailIds,
  phoneNumbers,
  emailAlertChkBox,
  smsAlertChkBox,
  notificationTime,
  userName
) => {
  const url = "/crtweb/update-user-details";
  const method = "get";
  const params = {
    emailIds,
    phoneNumbers,
    emailAlertChkBox,
    smsAlertChkBox,
    notificationTime,
    userName,
  };
  return baseRequest(url, method, params, null);
};
