export const addNewUser = (
  userName,
  password,
  railway,
  division,
  station,
  phoneNumber,
  emailId,
  createdBy,
  crtDevicesId
) => {
  const url = "/crtweb/add-new-user";
  const method = "get";
  const params = {
    userName: userName,
    password: password,
    railway: railway,
    division: division,
    station: station,
    phoneNumber: phoneNumber,
    emailId: emailId,
    createdBy: createdBy,
    crtDevicesId: crtDevicesId,
  };

  return baseRequest(url, method, params, null);
};

