import React, { Component } from "react";
import { Col, FormGroup, Label } from "reactstrap";
import { connect } from "react-redux";
import Select from "react-select";
import { BodyComponent, SubmitButton } from "../StyledComponents";
import "antd/dist/antd.css";
import { Layout, DatePicker } from "antd";
import ReactTable from "react-table";
import LayoutContainer from "../common/LayoutContainer";
import ResultModal from "../common/ResultModal";
import "react-table/react-table.css";
import { getUserCrtDevices } from "../../services/api/administration";
import { getIFDConnectHistory } from "../../services/api/reports";
import jsPDF from "jspdf";
import "jspdf-autotable";

const { Content } = Layout;

class IFDConnectHistory extends Component {
  state = {
    equipSelectedOption: null,
    showTable: false,
    showModal: false,
    options: [],
    loading: false,
  };
  tableColumns = [
    {
      Header: `IFD Connect History`,
      columns: [
        {
          accessor: "crt_id",
          numeric: false,
          disablePadding: false,
          Header: "Node Id",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "time_from",
          numeric: false,
          disablePadding: false,
          Header: "Time (from)",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "time_to",
          numeric: false,
          disablePadding: false,
          Header: "Time (to)",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "duration",
          numeric: false,
          disablePadding: false,
          Header: "Duration",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "status",
          numeric: false,
          disablePadding: false,
          Header: "Status",
          style: { whiteSpace: "unset" },
        },
      ],
    },
  ];
  // Api call
  componentDidMount = () => {
    const userName = this.props.user.user_name;
    getUserCrtDevices(userName)
      .then((resp) => {
        const data = resp.data;
        data.forEach((element) => {
          let crtDevice = {
            value: element.id,
            label: `${element.id} - ${element.railway} - ${element.division} - ${element.station}`,
          };
          this.setState({
            options: [...this.state.options, crtDevice],
          });
        });
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };
  viewIFDConnectHistory = () => {
    const crtId = this.state.equipSelectedOption;
    const startDate = Math.floor(new Date(this.state.startDate) / 1000);
    const endDate = Math.floor(new Date(this.state.endDate) / 1000);
    if (
      crtId == null ||
      this.state.startDate == null ||
      this.state.endDate == null
    ) {
      this.setState({
        showModal: true,
        modalHeader: "Wrong Entry",
        textColor: "text-danger",
        modalBody: "Please fill it properly",
      });
    } else {
      getIFDConnectHistory(crtId.value, startDate, endDate)
        .then((resp) => {
          this.setState({ showTable: true, data: resp.data });
          // console.log(this.state.data)
        })
        .catch((err) => {
          console.log({ err }, "error while calling api");
          this.setState({
            showModal: true,
            modalHeader: "Error",
            textColor: "text-danger",
            modalBody: err.message,
          });
        });
    }
  };

  downloadExcelSensorReport = () => {
    const crtId = this.state.equipSelectedOption;
    const startDate = Math.floor(new Date(this.state.startDate) / 1000);
    const endDate = Math.floor(new Date(this.state.endDate) / 1000);
    if (
      crtId == null ||
      this.state.startDate == null ||
      this.state.endDate == null
    ) {
      this.setState({
        showModal: true,
        modalHeader: "Wrong Entry",
        textColor: "text-danger",
        modalBody: "Please fill it properly",
      });
    } else {
      getIFDConnectHistory(crtId.value, startDate, endDate)
        .then((resp) => {
          const data = resp.data;
          let csvContent =
            "data:text/csv;charset=utf-8,Node Id, Time (from), Time (to), Status, Duration\n";
          data.forEach((element) => {
            csvContent =
              csvContent +
              `${element.crt_id},${element.time_from},${element.time_to},${element.status},${element.duration}\n`;
          });
          var encodedUri = encodeURI(csvContent);
          var link = document.createElement("a");
          link.setAttribute("href", encodedUri);
          link.setAttribute("download", "ifd-connect-history.csv");
          document.body.appendChild(link);
          link.click();
        })
        .catch((err) => {
          console.log({ err }, "error while calling api");
          this.setState({
            showModal: true,
            modalHeader: "Error",
            textColor: "text-danger",
            modalBody: err.message,
          });
        });
    }
  };
  downloadPdfSensorReport = () => {
    const crtId = this.state.equipSelectedOption;
    const startDate = Math.floor(new Date(this.state.startDate) / 1000);
    const endDate = Math.floor(new Date(this.state.endDate) / 1000);
    if (
      crtId == null ||
      this.state.startDate == null ||
      this.state.endDate == null
    ) {
      this.setState({
        showModal: true,
        modalHeader: "Wrong Entry",
        textColor: "text-danger",
        modalBody: "Please fill it properly",
      });
    } else {
      getIFDConnectHistory(crtId.value, startDate, endDate)
        .then((resp) => {
          const data = resp.data;
          let body = [];
          data.forEach((element) => {
            body.push([
              `${element.crt_id}`,
              `${element.time_from}`,
              `${element.time_to}`,
              `${element.status}`,
              `${element.duration}`,
            ]);
          });
          var doc = new jsPDF();
          doc.autoTable({
            head: [
              ["Node Id", "Time (from)", "Time (to)", "Status", "Duration"],
            ],
            body: body,
          });
          doc.save("ifd-connect-history.pdf");

          // console.log(this.state.data)
        })
        .catch((err) => {
          console.log({ err }, "error while calling api");
          this.setState({
            showModal: true,
            modalHeader: "Error",
            textColor: "text-danger",
            modalBody: err.message,
          });
        });
    }
  };
  // Handle Change
  equipHandleChange = (equipSelectedOption) => {
    this.setState({ equipSelectedOption });
  };
  handleChangeStartDate = (e) => {
    this.setState({ startDate: e._d });
  };
  handleChangeEndDate = (e) => {
    this.setState({ endDate: e._d });
  };
  render() {
    return (
      <LayoutContainer>
        <Content
          className="site-layout-background"
          style={{
            padding: "36px 60px",
            background: "#F2F2F2",
          }}
        >
          <h3>
            <u>IFD Connect History</u>
          </h3>
          <FormGroup row>
            <Label sm={1}>Equipment:</Label>
            <Col sm={3}>
              <Select
                value={this.state.equipSelectedOption}
                placeholder="id - railway - division - station"
                onChange={this.equipHandleChange}
                options={this.state.options}
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={1}>Start date/time:</Label>
            <Col sm={3}>
              <DatePicker
                showTime={{ format: "HH:mm" }}
                format="YYYY-MM-DD HH:mm"
                onChange={this.handleChangeStartDate}
                style={{ width: "100%" }}
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={1}>End date/time:</Label>
            <Col sm={3}>
              <DatePicker
                showTime={{ format: "HH:mm" }}
                format="YYYY-MM-DD HH:mm"
                onChange={this.handleChangeEndDate}
                style={{ width: "100%" }}
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <SubmitButton
              className="bg-primary"
              onClick={this.viewIFDConnectHistory}
            >
              View Report
            </SubmitButton>
            <SubmitButton
              className="bg-primary"
              onClick={this.downloadExcelSensorReport}
            >
              Download Excel
            </SubmitButton>
            <SubmitButton
              className="bg-primary"
              onClick={this.downloadPdfSensorReport}
            >
              Download Pdf
            </SubmitButton>
          </FormGroup>
          {this.state.showTable && (
            <ReactTable
              data={this.state.data}
              columns={this.tableColumns}
              loading={this.state.loading}
              className="-striped -highlight"
              defaultSorted={[
                {
                  id: "id",
                  desc: true,
                },
              ]}
            />
          )}
          {this.state.showModal && (
            <ResultModal
              modalHeader={this.state.modalHeader}
              modalBody={this.state.modalBody}
              textColor={this.state.textColor}
              closeModal={() => this.setState({ showModal: false })}
            />
          )}
        </Content>
      </LayoutContainer>
    );
  }
}
const mapStateToProps = (state) => {
  const user = state.user;
  return { user };
};
export default connect(mapStateToProps)(IFDConnectHistory);
