import React, { Component } from "react";
import { Label, Col, Input, Button } from "reactstrap";
import { connect } from "react-redux";
import {
  BodyComponent,
  FormContainer,
  RowContainer,
  SubmitButton,
} from "../StyledComponents";
import "antd/dist/antd.css";
import { Layout } from "antd";
import ResultModal from "../common/ResultModal";
import {
  getUserDetails,
  updateUserDetails,
} from "../../services/api/administration";
import LayoutContainer from "../common/LayoutContainer";
import Select from "react-select";
import { EditOutlined } from "@ant-design/icons";
import AddEmailIdModal from "./AddEmailIdModal";
import AddPhoneNumberModal from "./AddPhoneNumberModal";
const { Content } = Layout;
const showOption = [
  { value: "12am", label: "00:00" },
  { value: "1am", label: "01:00" },
  { value: "2am", label: "02:00" },
  { value: "3am", label: "03:00" },
  { value: "4am", label: "04:00" },
  { value: "5am", label: "05:00" },
  { value: "6am", label: "06:00" },
  { value: "7am", label: "07:00" },
  { value: "8am", label: "08:00" },
  { value: "9am", label: "09:00" },
  { value: "10am", label: "10:00" },
  { value: "11am", label: "11:00" },
  { value: "12pm", label: "12:00" },
  { value: "1pm", label: "13:00" },
  { value: "2pm", label: "14:00" },
  { value: "3pm", label: "15:00" },
  { value: "4pm", label: "16:00" },
  { value: "5pm", label: "17:00" },
  { value: "6pm", label: "18:00" },
  { value: "7pm", label: "19:00" },
  { value: "8pm", label: "20:00" },
  { value: "9pm", label: "21:00" },
  { value: "10pm", label: "22:00" },
  { value: "11pm", label: "23:00" },
];

class MyAccountDetails extends Component {
  state = {
    notificationAlertTime: null,
    data: [],
    emailIds: [],
    phoneNumbers: [],
    emailAlertChkBox: 0,
    smsAlertChkBox: 0,
    showModal: false,
    showAddEmailIdModal: false,
    showAddPhoneNumberModal: false,
    refresdata: false,
    phoneNumberEdit: false,
    emailAlertEdit: false,
    emailIdEdit: false,
    smsAlertEdit: false,
    notificationEdit: false,
    validateEmail: false,
    validatePhoneNumberLength: false,
  };
  // Api call
  componentDidMount = () => {
    this.getUserDetail();
  };

  componentDidUpdate = (prevProps, prevState) => {
    if (prevState.refresdata !== this.state.refresdata) {
      this.getUserDetail();
    }
  };
  updateUserDetails = () => {
    const emailAlertChkBox = this.state.emailAlertChkBox;
    const smsAlertChkBox = this.state.smsAlertChkBox;
    const notificationTime = this.state.notificationAlertTime.value;
    const userName = this.props.user.user_name;
    this.state.emailIds.forEach((element) => {
      this.validateEmail(element);
    });

    Promise.all(this.state.phoneNumbers)
      .then((result) => {
        console.log({ result });
        result.forEach((element) => {
          this.validatePhoneNumberLength(element);
        });
      })
      .then(() => {
        Promise.all(this.state.emailIds).then((result) => {
          console.log({ result });
          result.forEach((element) => {
            this.validateEmail(element);
          });
        });
      })
      .then(() => {
        const phoneNumbers = this.state.phoneNumbers.join(",");
        const emailIds = this.state.emailIds.join(",");
        if (this.state.validateEmail && this.state.validatePhoneNumberLength) {
          updateUserDetails(
            emailIds,
            phoneNumbers,
            emailAlertChkBox,
            smsAlertChkBox,
            notificationTime,
            userName
          )
            .then((resp) => {
              this.setState({
                phoneNumberEdit: false,
                emailAlertEdit: false,
                emailIdEdit: false,
                smsAlertEdit: false,
                notificationEdit: false,
                showModal: true,
                refresdata: !this.state.refresdata,
                modalHeader: "Success",
                textColor: "text-success",
                modalBody: "User detail updated",
              });
            })
            .catch((err) => {
              console.log({ err }, "error while calling api");
              this.setState({
                showModal: true,
                modalHeader: "Error",
                textColor: "text-danger",
                modalBody: `Error - ${err.message}`,
              });
            });
        }
      });
    // this.state.phoneNumbers.forEach((element) => {
    //   this.validatePhoneNumberLength(element);
    // });
  };
  getUserDetail = () => {
    const userName = this.props.user.user_name;
    getUserDetails(userName)
      .then((resp) => {
        console.log({ resp });
        showOption.forEach((element) => {
          if (element.value === resp.details.notification_time) {
            this.setState({
              notificationAlertTime: element,
              notificationAlertTimeShow: element.label,
            });
          }
        });
        this.setState({
          data: resp.details,
          emailIds: resp.details["email_id"].split(","),
          phoneNumbers: resp.details["phone_number"].split(","),
          emailAlertChkBox: resp.details.email_alert,
          smsAlertChkBox: resp.details.sms_alert,
        });
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };

  // Handle Change
  handleChangeEmailId1 = (e) => {
    let emailIds = this.state.emailIds;
    emailIds[0] = e.target.value;
    this.setState({ emailIds: emailIds });
  };
  handleChangeEmailId2 = (e) => {
    let emailIds = this.state.emailIds;
    emailIds[1] = e.target.value;
    this.setState({ emailIds: emailIds });
  };
  handleChangeEmailId3 = (e) => {
    let emailIds = this.state.emailIds;
    emailIds[2] = e.target.value;
    this.setState({ emailIds: emailIds });
  };
  handleChangePhoneNumber1 = (e) => {
    let phoneNumbers = this.state.phoneNumbers;
    phoneNumbers[0] = e.target.value;
    this.setState({ phoneNumbers: phoneNumbers });
  };
  handleChangePhoneNumber2 = (e) => {
    let phoneNumbers = this.state.phoneNumbers;
    phoneNumbers[1] = e.target.value;
    this.setState({ phoneNumbers: phoneNumbers });
  };
  handleChangePhoneNumber3 = (e) => {
    let phoneNumbers = this.state.phoneNumbers;
    phoneNumbers[2] = e.target.value;
    this.setState({ phoneNumbers: phoneNumbers });
  };
  handleChangeEmailAlert = (e) => {
    if (!this.state.emailAlertChkBox) {
      this.setState({ emailAlertChkBox: 1 });
    } else {
      this.setState({ emailAlertChkBox: 0 });
    }
  };
  handleChangeSmsAlert = (e) => {
    if (!this.state.smsAlertChkBox) {
      this.setState({ smsAlertChkBox: 1 });
    } else {
      this.setState({ smsAlertChkBox: 0 });
    }
  };
  notificationHandleChange = (notificationAlertTime) => {
    this.setState({ notificationAlertTime });
  };

  // Handle edits
  handleEditEmailId = () => {
    this.setState({ emailIdEdit: true });
  };
  handleEditPhoneNumber = () => {
    this.setState({ phoneNumberEdit: true });
  };
  handleEditEmailAlert = () => {
    this.setState({ emailAlertEdit: true });
  };
  handleEditSmsAlert = () => {
    this.setState({ smsAlertEdit: true });
  };
  handleEditNotification = () => {
    this.setState({ notificationEdit: true });
  };
  // Show
  // Show email Ids
  showEmailIds = () => {
    let resultReturn = [];

    if (this.state.emailIds.length >= 1) {
      if (this.state.data["email_id"].length) {
        resultReturn.push(
          <Col xs="5">
            {!this.state.emailIdEdit && (
              <div>
                <Label>{this.state.emailIds[0]}</Label>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <EditOutlined
                  className="text-primary"
                  onClick={this.handleEditEmailId}
                />
              </div>
            )}
            {this.state.emailIdEdit && (
              <Input
                type="text"
                id="emailId1"
                name="emailId1"
                placeholder=""
                style={{ marginBottom: 5 }}
                onChange={this.handleChangeEmailId1}
                defaultValue={this.state.emailIds[0]}
              />
            )}
          </Col>
        );
      }
    }
    if (this.state.emailIds.length >= 2) {
      resultReturn.push(
        <Col xs="5">
          <Label></Label>
        </Col>
      );
      resultReturn.push(
        <Col xs="5">
          {!this.state.emailIdEdit && (
            <div>
              <Label>{this.state.emailIds[1]}</Label>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <EditOutlined
                className="text-primary"
                onClick={this.handleEditEmailId}
              />
            </div>
          )}
          {this.state.emailIdEdit && (
            <Input
              type="text"
              id="emailId2"
              name="emailId2"
              placeholder=""
              style={{ marginBottom: 5 }}
              onChange={this.handleChangeEmailId2}
              defaultValue={this.state.emailIds[1]}
            />
          )}
        </Col>
      );
    }
    if (this.state.emailIds.length === 3) {
      resultReturn.push(
        <Col xs="5">
          <Label></Label>
        </Col>
      );
      resultReturn.push(
        <Col xs="5">
          {!this.state.emailIdEdit && (
            <div>
              <Label>{this.state.emailIds[2]}</Label>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <EditOutlined
                className="text-primary"
                onClick={this.handleEditEmailId}
              />
            </div>
          )}
          {this.state.emailIdEdit && (
            <Input
              type="text"
              id="emailId3"
              name="emailId3"
              placeholder=""
              style={{ marginBottom: 5 }}
              onChange={this.handleChangeEmailId3}
              defaultValue={this.state.emailIds[2]}
            />
          )}
        </Col>
      );
    }
    if (this.state.emailIds.length < 3) {
      resultReturn.push(
        <Col xs="5">
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <Button className="bg-primary" onClick={this.addEmailId}>
            Add Email Address
          </Button>
        </Col>
      );
    }
    return resultReturn;
  };
  showPhoneNumbers = () => {
    let resultReturn = [];
    if (this.state.phoneNumbers.length >= 1) {
      if (this.state.data["phone_number"]) {
        resultReturn.push(
          <Col xs="5">
            {!this.state.phoneNumberEdit && (
              <div>
                <Label>{this.state.phoneNumbers[0]}</Label>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <EditOutlined
                  className="text-primary"
                  onClick={this.handleEditPhoneNumber}
                />
              </div>
            )}
            {this.state.phoneNumberEdit && (
              <Input
                type="number"
                id="phoneNumber1"
                name="phoneNumber1"
                placeholder=""
                style={{ marginBottom: 5 }}
                onChange={this.handleChangePhoneNumber1}
                defaultValue={this.state.phoneNumbers[0]}
              />
            )}
          </Col>
        );
      }
    }
    if (this.state.phoneNumbers.length >= 2) {
      resultReturn.push(
        <Col xs="5">
          <Label></Label>
        </Col>
      );
      resultReturn.push(
        <Col xs="5">
          {!this.state.phoneNumberEdit && (
            <div>
              <Label>{this.state.phoneNumbers[1]}</Label>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <EditOutlined
                className="text-primary"
                onClick={this.handleEditPhoneNumber}
              />
            </div>
          )}
          {this.state.phoneNumberEdit && (
            <Input
              type="number"
              id="phoneNumber2"
              name="phoneNumber2"
              placeholder=""
              style={{ marginBottom: 5 }}
              onChange={this.handleChangePhoneNumber2}
              defaultValue={this.state.phoneNumbers[1]}
            />
          )}
        </Col>
      );
    }
    if (this.state.phoneNumbers.length === 3) {
      resultReturn.push(
        <Col xs="5">
          <Label></Label>
        </Col>
      );
      resultReturn.push(
        <Col xs="5">
          {!this.state.phoneNumberEdit && (
            <div>
              <Label>{this.state.phoneNumbers[2]}</Label>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <EditOutlined
                className="text-primary"
                onClick={this.handleEditPhoneNumber}
              />
            </div>
          )}
          {this.state.phoneNumberEdit && (
            <Input
              type="number"
              id="phoneNumber3"
              name="phoneNumber3"
              placeholder=""
              style={{ marginBottom: 5 }}
              onChange={this.handleChangePhoneNumber3}
              defaultValue={this.state.phoneNumbers[2]}
            />
          )}
        </Col>
      );
    }
    if (this.state.phoneNumbers.length < 3) {
      resultReturn.push(
        <Col xs="5">
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <Button className="bg-primary" onClick={this.addPhoneNumber}>
            Add Phone Number
          </Button>
        </Col>
      );
    }
    return resultReturn;
  };
  addEmailId = () => {
    this.setState({ showAddEmailIdModal: true });
  };
  addPhoneNumber = () => {
    this.setState({ showAddPhoneNumberModal: true });
  };
  // Validations
  validatePhoneNumberLength = (data) => {
    if (data.length === 10) {
      this.setState({ validatePhoneNumberLength: true });
    } else if (data.length === 0) {
      const index = this.state.phoneNumbers.indexOf(data);
      console.log({ index, data });
      if (index > -1) {
        this.state.phoneNumbers.splice(index, 1);
        this.setState({ validatePhoneNumberLength: true });
      }
    } else {
      this.setState({
        showModal: true,
        modalHeader: "Validation error",
        modalBody:
          "Phone Number is not valid. Please write valid phone number.",
        textColor: "text-danger",
        loading: false,
        validatePhoneNumberLength: false,
      });
    }
  };
  validateEmail = (inputText) => {
    var mailformat = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    console.log({ inputText }, inputText.match(mailformat));

    if (inputText.match(mailformat)) {
      this.setState({
        validateEmail: true,
      });
    } else if (inputText.length === 0) {
      const index = this.state.emailIds.indexOf(inputText);
      console.log({ index, inputText });

      if (index > -1) {
        this.state.emailIds.splice(index, 1);
        this.setState({
          validateEmail: true,
        });
      }
    } else {
      this.setState({
        showModal: true,
        modalHeader: "Validation error",
        modalBody: "Email Id is not valid. Please write valid email Id.",
        textColor: "text-danger",
        validateEmail: false,
        loading: false,
      });
    }
  };
  render() {
    return (
      <LayoutContainer>
        <Content
          className="site-layout-background"
          style={{
            padding: "36px 60px",
            background: "#F2F2F2",
          }}
        >
          <h3>
            <u>My Account Details</u>
          </h3>
          <FormContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;User Name:
                </Label>
              </Col>
              <Col xs="5">
                <Label>{this.state.data["user_name"]}</Label>
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Railway:
                </Label>
              </Col>
              <Col xs="5">
                <Label>{this.state.data["railway"]}</Label>
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Division:
                </Label>
              </Col>
              <Col xs="5">
                <Label>{this.state.data["division"]}</Label>
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Station:
                </Label>
              </Col>
              <Col xs="5">
                <Label>{this.state.data["station"]}</Label>
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Email
                  addresses:
                </Label>
              </Col>
              {this.showEmailIds()}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Phone Numbers:
                </Label>
              </Col>
              {this.showPhoneNumbers()}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Email alert:
                </Label>
              </Col>
              {!this.state.emailAlertEdit && (
                <Col xs="5">
                  {(() => {
                    if (this.state.emailAlertChkBox) {
                      return <Label>Yes</Label>;
                    } else {
                      return <Label>No</Label>;
                    }
                  })()}
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <EditOutlined
                    className="text-primary"
                    onClick={this.handleEditEmailAlert}
                  />
                </Col>
              )}
              {this.state.emailAlertEdit && (
                <Col xs="5">
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <Input
                    type="checkbox"
                    id="emailAlert"
                    name="emailAlert"
                    placeholder=""
                    onChange={this.handleChangeEmailAlert}
                    defaultChecked={this.state.emailAlertChkBox}
                  />
                </Col>
              )}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;SMS alert:
                </Label>
              </Col>
              {!this.state.smsAlertEdit && (
                <Col xs="5">
                  {(() => {
                    if (this.state.smsAlertChkBox) {
                      return <Label>Yes</Label>;
                    } else {
                      return <Label>No</Label>;
                    }
                  })()}
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <EditOutlined
                    className="text-primary"
                    onClick={this.handleEditSmsAlert}
                  />
                </Col>
              )}
              {this.state.smsAlertEdit && (
                <Col xs="5">
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <Input
                    type="checkbox"
                    id="smsAlert"
                    name="smsAlert"
                    placeholder=""
                    onChange={this.handleChangeSmsAlert}
                    defaultChecked={this.state.smsAlertChkBox}
                  />
                </Col>
              )}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Notification
                  alert time:
                </Label>
              </Col>
              {!this.state.notificationEdit && (
                <Col xs="5">
                  <Label>{this.state.notificationAlertTimeShow}</Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <EditOutlined
                    className="text-primary"
                    onClick={this.handleEditNotification}
                  />
                </Col>
              )}
              {this.state.notificationEdit && (
                <Col xs="5">
                  <Select
                    value={this.state.notificationAlertTime}
                    onChange={this.notificationHandleChange}
                    options={showOption}
                  />
                </Col>
              )}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Created On:
                </Label>
              </Col>
              <Col xs="5">
                <Label>{this.state.data["created_at"]}</Label>
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <SubmitButton
                  className="bg-primary"
                  onClick={this.updateUserDetails}
                >
                  Save Changes
                </SubmitButton>
              </Col>
            </RowContainer>
          </FormContainer>
          {this.state.showAddEmailIdModal && (
            <AddEmailIdModal
              userName={this.props.user.user_name}
              closeModal={() => {
                this.setState({ showAddEmailIdModal: false });
                this.getUserDetail();
              }}
            />
          )}
          {this.state.showAddPhoneNumberModal && (
            <AddPhoneNumberModal
              userName={this.props.user.user_name}
              closeModal={() => {
                this.setState({ showAddPhoneNumberModal: false });
                this.getUserDetail();
              }}
            />
          )}
          {this.state.showModal && (
            <ResultModal
              modalHeader={this.state.modalHeader}
              modalBody={this.state.modalBody}
              textColor={this.state.textColor}
              closeModal={() => this.setState({ showModal: false })}
            />
          )}
        </Content>
      </LayoutContainer>
    );
  }
}

const mapStateToProps = (state) => {
  const user = state.user;
  return { user };
};
export default connect(mapStateToProps)(MyAccountDetails);
