const RouterContainer = styled.div`
  display: flex;
  flex: 1;
`;

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modalVisible: true,
    };
    console.log({ props });
  }

  hideModal = () => {
    this.props.hideError();
  };

  render() {
    return (
      <OuterContainer>
        <Router>
          <RouterContainer>
            <Route path="/" exact component={LoginScreen} />

            <Route path="/home" exact name="Home" component={Home} />
            <Route
              path="/account-details"
              exact
              name="My Account Details"
              component={MyAccountDetails}
            />
            <Route
              path="/add-users"
              exact
              name="Add Users"
              component={AddUsers}
            />
            <Route
              path="/all-users"
              exact
              name="All Users"
              component={AllUsers}
            />
            <Route
              path="/sensor-report"
              exact
              name="Sensor Report"
              component={SensorReport}
            />
            <Route
              path="/IFD-connect-history"
              exact
              name="IFD Connect History"
              component={IFDConnectHistory}
            />
            <Route
              path="/crt-devices"
              exact
              name="CRT devices"
              component={CrtDevices}
            />
          </RouterContainer>
        </Router>
        {/* {this.props.appError.hasError && (
          <ErrorModal>
            <ModalContainer>
              <ModalBody>
                <CloseContainer onTap={this.hideModal}>
                  <FaTimes />
                </CloseContainer>
                <ModalIcon src={modalIcon} />
                <ModalTitle>{this.props.appError.errorTitle}</ModalTitle>
                <ModalText>{this.props.appError.errorDesc}</ModalText>
              </ModalBody>
            </ModalContainer>
          </ErrorModal>
        )} */}
      </OuterContainer>
    );
  }
}

const mapStateToProps = (state) => ({
  appError: state.app.appError,
});

const mapDispatchToProps = (dispatch) => ({
  hideError: () => {
    dispatch(hideError());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
