import React, { Component } from "react";
import {
  Modal,
  ModalBody,
  ModalFooter,
  Button,
  Label,
  Input,
  Col,
  Row,
} from "reactstrap";
import "react-table/react-table.css";
import styled from "styled-components";
import { FaTimes } from "react-icons/fa";
import { addEmailId } from "../../services/api/administration";
import StyledTappable from "../common/StyledTappable";
import ResultModal from "../common/ResultModal";
import ConfirmRemoveDeviceModal from "./ConfirmRemoveDeviceModal";

const CloseModal = styled(FaTimes)``;

const HeaderComponent = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1px 10px;
`;
const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

class AddEmailIdModal extends Component {
  state = { emailId: "" };
  addEmailId = () => {
    const userName = this.props.userName;
    const emailId = this.state.emailId;
    if (this.validateEmail(emailId)) {
      addEmailId(userName, emailId)
        .then((resp) => {
          this.props.closeModal();
        })
        .catch((err) => {
          console.log(err, "[fetchTableData]");
          this.setState({
            showModal: true,
            modalHeader: "Error",
            textColor: "text-danger",
            modalBody: err.message,
          });
        });
    }
  };
  handleChangeEmailId = (e) => {
    this.setState({ emailId: e.target.value });
  };
  validateEmail = (inputText) => {
    var mailformat = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (inputText.match(mailformat)) {
      return true;
    } else if (inputText.length === 0) {
      this.setState({
        showModal: true,
        modalHeader: "Note",
        modalBody: "The text field is empty.Please write valid email Id.",
        textColor: "text-primary",
      });
      return false;
    } else {
      this.setState({
        showModal: true,
        modalHeader: "Validation error",
        modalBody: "Email Id is not valid. Please write valid email Id.",
        textColor: "text-danger",
      });
      return false;
    }
  };
  render() {
    return (
      <Modal isOpen toggle={this.props.closeModal} style={customStyles}>
        <HeaderComponent>
          <h3 className="text-primary" style={{ paddingTop: 5 }}>
            Add New
          </h3>
          <StyledTappable onTap={this.props.closeModal}>
            <CloseModal />
          </StyledTappable>
        </HeaderComponent>
        <ModalBody>
          <Row>
            <Label style={{ marginTop: 5, marginLeft: 15, marginRight: 15 }}>
              Email Id:
            </Label>
            <Col xs="8">
              <Input
                type="text"
                id="emailId"
                name="emailId"
                placeholder=""
                onChange={this.handleChangeEmailId}
              />
            </Col>
          </Row>
        </ModalBody>
        <ModalFooter>
          <Button className="bg-primary" onClick={this.addEmailId}>
            Add
          </Button>
          <Button onClick={this.props.closeModal}>Cancel</Button>
        </ModalFooter>
        {this.state.showRemoveDeviceModal && (
          <ConfirmRemoveDeviceModal
            removeCrtDevice={() => {
              this.removeCrtDevice(this.state.deviceUserDetails);
            }}
            closeModal={() => this.setState({ showRemoveDeviceModal: false })}
          />
        )}
        {this.state.showModal && (
          <ResultModal
            modalHeader={this.state.modalHeader}
            modalBody={this.state.modalBody}
            textColor={this.state.textColor}
            closeModal={() => this.setState({ showModal: false })}
          />
        )}
      </Modal>
    );
  }
}

export default AddEmailIdModal;
