import React, { Component } from "react";
import { BodyComponent } from "../StyledComponents";
import "antd/dist/antd.css";
import { Layout } from "antd";
import {
  getAllUser,
  getUserCrtDevices,
  deleteUser,
} from "../../services/api/administration";
import LayoutContainer from "../common/LayoutContainer";
import ReactTable from "react-table";
import "react-table/react-table.css";
import ResultModal from "../common/ResultModal";
import ShowCrtModal from "./ShowCrtModal";
import { AiOutlineSelect, AiFillDelete } from "react-icons/ai";
import StyledTappable from "../common/StyledTappable";
import ConfirmDeleteUserModal from "./ConfirmDeleteUserModal";
const { Content } = Layout;
class AllUsers extends Component {
  state = {
    loading: true,
    showCrtModal: false,
    showModal: false,
    data: [],
    modalHeader: "",
    userCrtDevices: [],
    deleteUserName: "",
    showDeleteUserModal: false,
  };
  tableColumns = [
    {
      Header: `All Users Details`,
      columns: [
        {
          accessor: "id",
          numeric: false,
          disablePadding: false,
          Header: "User Id",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "user_name",
          numeric: false,
          disablePadding: false,
          Header: "User Name",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "railway",
          numeric: false,
          disablePadding: false,
          Header: "Railway",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "division",
          numeric: false,
          disablePadding: false,
          Header: "Division",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "station",
          numeric: false,
          disablePadding: false,
          Header: "Station",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "phone_number",
          numeric: false,
          disablePadding: false,
          Header: "Phone Number",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "email_id",
          numeric: false,
          disablePadding: false,
          Header: "Email Id",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "created_at",
          numeric: false,
          disablePadding: false,
          Header: "Created At",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "created_by",
          numeric: false,
          disablePadding: false,
          Header: "Created By",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "crt_devices",
          numeric: false,
          disablePadding: false,
          Header: "Actions",
          style: { whiteSpace: "unset" },
          Cell: (row) => (
            <div>
              <StyledTappable
                className="text-primary"
                onTap={() => this.showCrtDevices(row)}
              >
                <AiOutlineSelect style={{ marginRight: 5 }} />
                CRT Devices
              </StyledTappable>
              <StyledTappable
                className="text-danger"
                onTap={() => this.showDeleteUserModel(row)}
              >
                <AiFillDelete style={{ marginRight: 5 }} />
                Delete User
              </StyledTappable>
            </div>
          ),
        },
      ],
    },
  ];
  fetchTableData = () => {
    this.setState({
      loading: true,
    });
    getAllUser()
      .then((resp) => {
        this.setState({
          data: resp.allUsers,
          loading: false,
        });
      })
      .catch((err) => {
        console.log(err, "[fetchTableData]");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };
  showCrtDevices = (row) => {
    const userName = row.row.user_name;
    getUserCrtDevices(userName)
      .then((resp) => {
        this.setState({
          userCrtDevices: resp.data,
          showCrtModal: true,
          userName: userName,
        });
      })
      .catch((err) => {
        console.log(err, "[fetchTableData]");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };
  showDeleteUserModel = (row) => {
    const userName = `${row.row.user_name}`;
    this.setState({ showDeleteUserModal: true, deleteUserName: userName });
  };
  deleteUser = (userName) => {
    // const userName = `${row.row.user_name}`;
    console.log({ userName });

    deleteUser(userName)
      .then((resp) => {
        this.fetchTableData();
        this.setState({ showDeleteUserModal: false });
      })
      .catch((err) => {
        console.log(err, "[deleteUser]");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };
  render() {
    return (
      <LayoutContainer>
        <Content
          className="site-layout-background"
          style={{
            padding: "36px 60px",
            background: "#F2F2F2",
          }}
        >
          <h3>
            <u>All Users</u>
          </h3>
          <ReactTable
            data={this.state.data}
            columns={this.tableColumns}
            loading={this.state.loading}
            onFetchData={this.fetchTableData}
            showPaginationTop={false}
            className="-striped -highlight"
            defaultSorted={[
              {
                id: "id",
                desc: true,
              },
            ]}
          />
          {this.state.showCrtModal && (
            <ShowCrtModal
              userName={this.state.userName}
              userCrtDevices={this.state.userCrtDevices}
              closeModal={() => this.setState({ showCrtModal: false })}
            />
          )}
          {this.state.showModal && (
            <ResultModal
              modalHeader={this.state.modalHeader}
              modalBody={this.state.modalBody}
              textColor={this.state.textColor}
              closeModal={() => this.setState({ showModal: false })}
            />
          )}
          {this.state.showDeleteUserModal && (
            <ConfirmDeleteUserModal
              deleteUser={() => {
                this.deleteUser(this.state.deleteUserName);
              }}
              closeModal={() => this.setState({ showDeleteUserModal: false })}
            />
          )}
        </Content>
      </LayoutContainer>
    );
  }
}

export default AllUsers;
