import React, { Component } from "react";
import { Label, Col, Input } from "reactstrap";
import {
  addNewUser,
  getAllCrtDevices,
} from "../../services/api/administration";
import Select from "react-select";
import {
  BodyComponent,
  FormContainer,
  RowContainer,
  SubmitButton,
} from "../StyledComponents";
import "antd/dist/antd.css";
import LayoutContainer from "../common/LayoutContainer";
import ResultModal from "../common/ResultModal";
import { Layout } from "antd";
const { Content } = Layout;

class MyAccountDetails extends Component {
  state = {
    userName: "",
    showModal: false,
    password: "",
    railway: "",
    division: "",
    station: "",
    phoneNumber: null,
    emailId: "",
    createdBy: "",
    crtDevicesId: [],
    allCrtDevices: [],
  };
  // Api call
  componentDidMount = () => {
    getAllCrtDevices()
      .then((resp) => {
        const data = resp.data;
        data.forEach((element) => {
          let crtDevice = {
            value: element.id,
            label: `${element.id}-${element.railway}-${element.division}-${element.station}`,
          };
          this.setState({
            allCrtDevices: [...this.state.allCrtDevices, crtDevice],
          });
        });
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };
  addNewUser = () => {
    const userName = this.state.userName;
    const password = this.state.password;
    const railway = this.state.railway;
    const division = this.state.division;
    const station = this.state.station;
    const phoneNumber = this.state.phoneNumber;
    const emailId = this.state.emailId;
    const createdBy = this.state.createdBy;
    const crtDevicesId = this.state.crtDevicesId;
    if (
      !(
        userName.length &&
        password.length &&
        railway.length &&
        division.length &&
        station.length &&
        phoneNumber &&
        emailId.length &&
        createdBy.length &&
        crtDevicesId.length
      )
    ) {
      this.setState({
        showModal: true,
        modalHeader: "Wrong Entry",
        textColor: "text-danger",
        modalBody: "Please fill it properly",
      });
    } else {
      addNewUser(
        userName,
        password,
        railway,
        division,
        station,
        phoneNumber,
        emailId,
        createdBy,
        crtDevicesId
      )
        .then((resp) => {
          this.setState({
            showModal: true,
            modalHeader: "Success",
            textColor: "text-success",
            modalBody: "New User Added",
          });
        })
        .catch((err) => {
          console.log({ err }, "error while calling api");
          this.setState({
            showModal: true,
            modalHeader: "Error",
            textColor: "text-danger",
            modalBody: err.message,
          });
        });
    }
  };

  // Handle Change
  handleChangeUserName = (e) => {
    this.setState({ userName: e.target.value });
  };

  handleChangePassword = (e) => {
    this.setState({ password: e.target.value });
  };
  handleChangeRailway = (e) => {
    this.setState({ railway: e.target.value });
  };
  handleChangeDivision = (e) => {
    this.setState({ division: e.target.value });
  };
  handleChangeStation = (e) => {
    this.setState({ station: e.target.value });
  };
  handleChangePhoneNumber = (e) => {
    this.setState({ phoneNumber: e.target.value });
  };
  handleChangeEmailId = (e) => {
    this.setState({ emailId: e.target.value });
  };
  handleChangeCreatedBy = (e) => {
    this.setState({ createdBy: e.target.value });
  };
  handleChangeCrtDevicesId = (e) => {
    if (!e.length) {
      this.setState({ crtDevicesId: [] });
    } else {
      const crtDevicesId = [];
      e.forEach((element) => {
        crtDevicesId.push(element.value);
        this.setState({ crtDevicesId: crtDevicesId });
      });
    }
  };
  render() {
    return (
      <LayoutContainer>
        <Content
          className="site-layout-background"
          style={{
            padding: "36px 60px",
            background: "#F2F2F2",
          }}
        >
          <h3>
            <u>Add New User</u>
          </h3>
          <FormContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; User Name:
                </Label>
              </Col>
              <Col xs="5">
                <Input
                  type="text"
                  id="userName"
                  name="userName"
                  placeholder=""
                  onChange={this.handleChangeUserName}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Password:
                </Label>
              </Col>
              <Col xs="5">
                <Input
                  type="password"
                  id="password"
                  name="password"
                  placeholder=""
                  onChange={this.handleChangePassword}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Railway:
                </Label>
              </Col>
              <Col xs="5">
                <Input
                  type="text"
                  id="railway"
                  name="railway"
                  placeholder=""
                  onChange={this.handleChangeRailway}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Division:
                </Label>
              </Col>
              <Col xs="5">
                <Input
                  type="text"
                  id="division"
                  name="division"
                  placeholder=""
                  onChange={this.handleChangeDivision}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Station:
                </Label>
              </Col>
              <Col xs="5">
                <Input
                  type="text"
                  id="station"
                  name="station"
                  placeholder=""
                  onChange={this.handleChangeStation}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Phone Number:
                </Label>
              </Col>
              <Col xs="5">
                <Input
                  type="text"
                  id="phoneNumber"
                  name="phoneNumber"
                  placeholder=""
                  onChange={this.handleChangePhoneNumber}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Email Id:
                </Label>
              </Col>
              <Col xs="5">
                <Input
                  type="text"
                  id="emailId"
                  name="emailId"
                  placeholder=""
                  onChange={this.handleChangeEmailId}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Created By:
                </Label>
              </Col>
              <Col xs="5">
                <Input
                  type="text"
                  id="createdBy"
                  name="createdBy"
                  placeholder=""
                  onChange={this.handleChangeCreatedBy}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; CRT Devices:
                </Label>
              </Col>
              <Col xs="5">
                <Select
                  isMulti
                  name="crtDevices"
                  placeholder="id-railway-division-station"
                  options={this.state.allCrtDevices}
                  className="basic-multi-select"
                  classNamePrefix="select"
                  onChange={this.handleChangeCrtDevicesId}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <SubmitButton className="bg-primary" onClick={this.addNewUser}>
                  Add User
                </SubmitButton>
              </Col>
            </RowContainer>
          </FormContainer>
          {this.state.showModal && (
            <ResultModal
              modalHeader={this.state.modalHeader}
              modalBody={this.state.modalBody}
              textColor={this.state.textColor}
              closeModal={() => this.setState({ showModal: false })}
            />
          )}
        </Content>
      </LayoutContainer>
    );
  }
}

export default MyAccountDetails;
