import React, { Component } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import ResultModal from "../common/ResultModal";
import { getSensorReportCompareTable } from "../../services/api/reports";

class CompareReports extends Component {
  state = {
    showTable: false,
    showModal: false,
    loading: false,
    data: [],
  };
  // Fetch Table Data
  onFetchData = (state) => {
    this.fetchTableData();
  };

  fetchTableData = () => {
    this.setState({
      loading: true,
    });
    const crtId = this.props.crtId;
    const params = {
      crtId,
      dates: this.props.dates,
      datesLimit: this.props.datesLimit,
    };
    console.log(params);
    getSensorReportCompareTable(params)
      .then((resp) => {
        const data = resp.data;
        this.setState({
          loading: false,
          data,
        });
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };
  render() {
    return (
      <div>
        <ReactTable
          data={this.state.data}
          columns={this.props.compareTableColumns}
          loading={this.state.loading}
          onFetchData={this.onFetchData}
          showPaginationBottom={false}
          defaultPageSize={24}
          className="-striped -highlight text-center"
        />
        {this.state.showModal && (
          <ResultModal
            modalHeader={this.state.modalHeader}
            modalBody={this.state.modalBody}
            textColor={this.state.textColor}
            closeModal={() => this.setState({ showModal: false })}
          />
        )}
      </div>
    );
  }
}
export default CompareReports;
