import React, { Component } from "react";
import {
  Modal,
  ModalBody,
  ModalFooter,
  Button,
  Label,
  Col,
  Row,
} from "reactstrap";
import "react-table/react-table.css";
import styled from "styled-components";
import { FaTimes } from "react-icons/fa";
import {
  getUserCrtDevices,
  getAllCrtDevices,
  updateUserCrtDevice,
} from "../../services/api/administration";
import StyledTappable from "../common/StyledTappable";
import ResultModal from "../common/ResultModal";
import Select from "react-select";
import ConfirmRemoveDeviceModal from "./ConfirmRemoveDeviceModal";

const CloseModal = styled(FaTimes)``;

const HeaderComponent = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1px 10px;
`;
const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

class ShowCrtModal extends Component {
  state = {
    loading: false,
    deviceUserDetails: [],
    userCrtDevices: [],
    allCrtDevices: [],
    selectedCrtDevices: [],
    crtDevicesId: [],
    updateFlag: 0,
  };
  componentDidMount = () => {
    getAllCrtDevices()
      .then((resp) => {
        const data = resp.data;
        data.forEach((element) => {
          let crtDevice = {
            value: element.id,
            label: `${element.id}-${element.railway}-${element.division}-${element.station}`,
          };
          this.setState({
            allCrtDevices: [...this.state.allCrtDevices, crtDevice],
          });
        });
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
    getUserCrtDevices(this.props.userName)
      .then((resp) => {
        const data = resp.data;
        data.forEach((element) => {
          let crtDevice = {
            value: element.id,
            label: `${element.id}-${element.railway}-${element.division}-${element.station}`,
          };
          this.setState({
            selectedCrtDevices: [...this.state.selectedCrtDevices, crtDevice],
            crtDevicesId: [...this.state.crtDevicesId, element.id],
          });
        });
      })
      .catch((err) => {
        console.log(err, "[fetchTableData]");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };
  showRemoveDeviceModel = (row) => {
    const deviceUserDetails = {
      crtId: row.row.id,
      userName: this.props.userName,
    };
    this.setState({ showRemoveDeviceModal: true, deviceUserDetails });
  };
  handleChangeCrtDevicesId = (e) => {
    if (e == null) {
      this.setState({ crtDevicesId: [], updateFlag: 1 });
    } else {
      const crtDevicesId = [];
      this.setState({ selectedCrtDevices: e });
      e.forEach((element) => {
        crtDevicesId.push(element.value);
        this.setState({ crtDevicesId: crtDevicesId, updateFlag: 1 });
      });
    }
  };
  saveChange = () => {
    console.log(this.state.crtDevicesId);
    if (this.state.updateFlag) {
      updateUserCrtDevice(this.props.userName, this.state.crtDevicesId)
        .then((resp) => {
          this.props.closeModal();
        })
        .catch((err) => {
          console.log(err, "[fetchTableData]");
          this.setState({
            showModal: true,
            modalHeader: "Error",
            textColor: "text-danger",
            modalBody: err.message,
          });
        });
    } else {
      this.setState({
        showModal: true,
        modalHeader: "Note",
        textColor: "text-primary",
        modalBody:
          "No change is dones or crt device access is empty.Atleast one device is connected to a user.",
      });
    }
  };
  render() {
    return (
      <Modal isOpen toggle={this.props.closeModal} style={customStyles}>
        <HeaderComponent>
          <h3 className="text-primary" style={{ paddingTop: 5 }}>
            CRT Devices
          </h3>
          <StyledTappable onTap={this.props.closeModal}>
            <CloseModal />
          </StyledTappable>
        </HeaderComponent>
        <ModalBody>
          <Row>
            <Label style={{ marginLeft: 15, marginRight: 40 }}>
              User Name:
            </Label>
            <Label>{this.props.userName}</Label>
          </Row>
          <Row>
            <Label style={{ marginTop: 5, marginLeft: 15, marginRight: 15 }}>
              CRT Devices:
            </Label>
            <Col xs="8">
              <Select
                isMulti
                value={this.state.selectedCrtDevices}
                name="crtDevices"
                placeholder="id-railway-division-station"
                options={this.state.allCrtDevices}
                className="basic-multi-select"
                classNamePrefix="select"
                onChange={this.handleChangeCrtDevicesId}
              />
            </Col>
          </Row>
        </ModalBody>
        <ModalFooter>
          <Button className="bg-primary" onClick={this.saveChange}>
            Save Change
          </Button>
          <Button onClick={this.props.closeModal}>Cancel</Button>
        </ModalFooter>
        {this.state.showRemoveDeviceModal && (
          <ConfirmRemoveDeviceModal
            removeCrtDevice={() => {
              this.removeCrtDevice(this.state.deviceUserDetails);
            }}
            closeModal={() => this.setState({ showRemoveDeviceModal: false })}
          />
        )}
        {this.state.showModal && (
          <ResultModal
            modalHeader={this.state.modalHeader}
            modalBody={this.state.modalBody}
            textColor={this.state.textColor}
            closeModal={() => this.setState({ showModal: false })}
          />
        )}
      </Modal>
    );
  }
}

export default ShowCrtModal;
