export * from './homeActions';

export function loginUser(email, password, history) {
  return {
    type: 'LOGIN_USER',
    payload: {
      email,
      password,
      history,
    },
  };
}

export function logoutUser() {
  return {
    type: 'LOGOUT_USER',
  };
}

export function setUserInfo(operatorDetails) {
  return {
    type: 'SET_USER_INFO',
    payload: {
      operatorDetails,
    },
  };
}

export function showError(errorTitle, errorDesc) {
  return {
    type: 'SHOW_ERROR',
    payload: {
      hasError: true,
      errorTitle,
      errorDesc,
    },
  };
}

export function hideError() {
  return {
    type: 'HIDE_ERROR',
    payload: {
      hasError: false,
      errorTitle: 'Something went wrong',
      errorDesc: 'Check your internet connection and try again',
    },
  };
}

export function setAuthToken(authToken) {
  return {
    type: 'AUTH_TOKEN',
    payload: authToken,
  };
}
