import React, { Component } from "react";
import { connect } from "react-redux";
import LayoutContainer from "../common/LayoutContainer";
import { Label, Col, Input } from "reactstrap";
import {
  updateCrtDeviceDetails,
  getCrtDevicesInfo,
} from "../../services/api/reports";
import ResultModal from "../common/ResultModal";
import {
  FormContainer,
  RowContainer,
  SubmitButton,
} from "../StyledComponents";
import "antd/dist/antd.css";
import { Layout } from "antd";
import { getUserCrtDevices } from "../../services/api/administration";
import Select from "react-select";

import { EditOutlined } from "@ant-design/icons";
const { Content } = Layout;

class MyAccountDetails extends Component {
  state = {
    equipSelectedOption: null,
    equipOptions: [],
    simNumber: "",
    maxTemp: "",
    minTemp: "",
    dataRetrivalTime: "",
    showModal: false,
    refresdata: false,
    simEdit: false,
    maxTempEdit: false,
    minTempEdit: false,
    dataRetrivalEdit: false,
    showadmin: false,
  };
  // Api call
  componentDidMount = () => {
    const userName = this.props.user.user_name;
    getUserCrtDevices(userName)
      .then((resp) => {
        const data = resp.data;
        data.forEach((element) => {
          let crtDevice = {
            value: element.id,
            label: `${element.id} - ${element.railway} - ${element.division} - ${element.station}`,
          };
          this.setState({
            equipOptions: [...this.state.equipOptions, crtDevice],
          });
        });
      })
      .then(() => {
        this.setState({
          equipSelectedOption: this.state.equipOptions[0],
        });
      })
      .then(() => {
        this.getCrtDeviceDetails();
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
    if (userName === "Rohit") {
      this.setState({
        showadmin: true,
      });
    }
    if (userName === "nayanagg") {
      this.setState({ showadmin: true });
    }
  };
  updateCrtDeviceDetails = () => {
    const crtId = this.state.equipSelectedOption.value;
    const simNumber = this.state.simNumber;
    const maxTemp = this.state.maxTemp;
    const minTemp = this.state.minTemp;
    const dataRetrivalTime = this.state.dataRetrivalTime;
    updateCrtDeviceDetails(crtId, simNumber, maxTemp, minTemp, dataRetrivalTime)
      .then((resp) => {
        this.setState({
          simEdit: false,
          maxTempEdit: false,
          minTempEdit: false,
          dataRetrivalEdit: false,
          showModal: true,
          refresdata: !this.state.refresdata,
          modalHeader: "Success",
          textColor: "text-success",
          modalBody: "Crt detail updated",
        });
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: `Error - ${err.message}`,
        });
      });
  };
  getCrtDeviceDetails = () => {
    const crtId = this.state.equipSelectedOption.value;
    getCrtDevicesInfo(crtId).then((resp) => {
      console.log(resp.data, "[get-crt-devices-info]");
      const crtData = resp.data[0];
      this.setState({
        simNumber: crtData.sim_number,
        maxTemp: crtData.max_temp,
        minTemp: crtData.min_temp,
        dataRetrivalTime: crtData.data_retrival_time,
      });
    });
  };
  componentDidUpdate = (prevProps, prevState) => {
    if (prevState.refresdata !== this.state.refresdata) {
      this.getCrtDeviceDetails();
    }
  };

  // Handle Change
  equipHandleChange = (equipSelectedOption) => {
    this.setState({
      equipSelectedOption,
      refresdata: !this.state.refresdata,
      simEdit: false,
      maxTempEdit: false,
      minTempEdit: false,
      dataRetrivalEdit: false,
    });
    // this.getCrtDeviceDetails();
  };
  handleChangeSimNumber = (e) => {
    this.setState({ simNumber: e.target.value });
  };
  handleChangeMaxTemp = (e) => {
    this.setState({ maxTemp: e.target.value });
  };
  handleChangeMinTemp = (e) => {
    this.setState({ minTemp: e.target.value });
  };
  handleChangeEmailAlert = (e) => {
    if (!this.state.emailAlertChkBox) {
      this.setState({ emailAlertChkBox: 1 });
    } else {
      this.setState({ emailAlertChkBox: 0 });
    }
  };
  handleChangeSmsAlert = (e) => {
    if (!this.state.smsAlertChkBox) {
      this.setState({ smsAlertChkBox: 1 });
    } else {
      this.setState({ smsAlertChkBox: 0 });
    }
  };
  handleChangeDataRetrivalTime = (e) => {
    this.setState({ dataRetrivalTime: e.target.value });
  };
  notificationHandleChange = (notificationAlertTime) => {
    this.setState({ notificationAlertTime });
  };

  // Handle edits
  handleEditSim = () => {
    this.setState({ simEdit: true });
  };
  handleEditMaxTemp = () => {
    this.setState({ maxTempEdit: true });
  };
  handleEditMinTemp = () => {
    this.setState({ minTempEdit: true });
  };
  handleEditDataRetrival = () => {
    this.setState({ dataRetrivalEdit: true });
  };
  render() {
    return (
      <LayoutContainer>
        <Content
          className="site-layout-background"
          style={{
            padding: "36px 60px",
            background: "#F2F2F2",
          }}
        >
          <h3>
            <u>CRT Device Details</u>
          </h3>
          <FormContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Equipment:
                </Label>
              </Col>
              <Col xs="5">
                <Select
                  value={this.state.equipSelectedOption}
                  placeholder="id - railway - division - station"
                  defaultValue={this.state.equipOptions[0]}
                  onChange={this.equipHandleChange}
                  options={this.state.equipOptions}
                />
              </Col>
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Sim Number:
                </Label>
              </Col>
              {!this.state.simEdit && (
                <Col xs="5">
                  <Label>{this.state.simNumber}</Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  {this.state.showadmin && (
                    <EditOutlined
                      className="text-primary"
                      onClick={this.handleEditSim}
                    />
                  )}
                </Col>
              )}
              {this.state.simEdit && (
                <Col xs="5">
                  <Input
                    type="text"
                    id="simNumber"
                    name="simNumber"
                    placeholder=""
                    onChange={this.handleChangeSimNumber}
                    defaultValue={this.state.simNumber}
                  />
                </Col>
              )}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Max.
                  Temperature:
                </Label>
              </Col>
              {!this.state.maxTempEdit && (
                <Col xs="5">
                  <Label>{this.state.maxTemp}</Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <EditOutlined
                    className="text-primary"
                    onClick={this.handleEditMaxTemp}
                  />
                </Col>
              )}
              {this.state.maxTempEdit && (
                <Col xs="5">
                  <Input
                    type="text"
                    id="maxTemp"
                    name="maxTemp"
                    placeholder=""
                    onChange={this.handleChangeMaxTemp}
                    defaultValue={this.state.maxTemp}
                  />
                </Col>
              )}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Min
                  Temperature:
                </Label>
              </Col>
              {!this.state.minTempEdit && (
                <Col xs="5">
                  <Label>{this.state.minTemp}</Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <EditOutlined
                    className="text-primary"
                    onClick={this.handleEditMinTemp}
                  />
                </Col>
              )}
              {this.state.minTempEdit && (
                <Col xs="5">
                  <Input
                    type="text"
                    id="minTemp"
                    name="minTemp"
                    placeholder=""
                    onChange={this.handleChangeMinTemp}
                    defaultValue={this.state.minTemp}
                  />
                </Col>
              )}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                <Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Data retrival
                  time:
                </Label>
              </Col>
              {!this.state.dataRetrivalEdit && (
                <Col xs="5">
                  <Label>{this.state.dataRetrivalTime} minutes</Label>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <EditOutlined
                    className="text-primary"
                    onClick={this.handleEditDataRetrival}
                  />
                </Col>
              )}
              {this.state.dataRetrivalEdit && (
                <Col xs="5">
                  <Input
                    type="number"
                    id="dataRetrivalTime"
                    name="dataRetrivalTime"
                    placeholder=""
                    onChange={this.handleChangeDataRetrivalTime}
                    defaultValue={this.state.dataRetrivalTime}
                  />
                </Col>
              )}
            </RowContainer>
            <RowContainer>
              <Col xs="5">
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <SubmitButton
                  className="bg-primary"
                  onClick={this.updateCrtDeviceDetails}
                >
                  Save Changes
                </SubmitButton>
              </Col>
            </RowContainer>
          </FormContainer>
          {this.state.showModal && (
            <ResultModal
              modalHeader={this.state.modalHeader}
              modalBody={this.state.modalBody}
              textColor={this.state.textColor}
              closeModal={() => this.setState({ showModal: false })}
            />
          )}
        </Content>
      </LayoutContainer>
    );
  }
}

const mapStateToProps = (state) => {
  const user = state.user;
  return { user };
};
export default connect(mapStateToProps)(MyAccountDetails);
