import React, { Component } from "react";
import { Modal, ModalBody, ModalFooter, Button } from "reactstrap";
import styled from "styled-components";
import { FaTimes } from "react-icons/fa";
import StyledTappable from "../common/StyledTappable";

const CloseModal = styled(FaTimes)``;

const HeaderComponent = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1px 10px;
`;
const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

class ConfirmRemoveDeviceModal extends Component {
  render() {
    return (
      <Modal isOpen toggle={this.props.closeModal} style={customStyles}>
        <HeaderComponent>
          <h3 className="text-danger" style={{ paddingTop: 5 }}>
            Remove CRT Device
          </h3>
          <StyledTappable onTap={this.props.closeModal}>
            <CloseModal />
          </StyledTappable>
        </HeaderComponent>
        <ModalBody>
          Are you sure, you want to remove this device. This is permanent.
        </ModalBody>
        <ModalFooter>
          <Button className="bg-danger" onClick={this.props.removeCrtDevice}>
            Remove
          </Button>
          <Button onClick={this.props.closeModal}>Cancel</Button>
        </ModalFooter>
      </Modal>
    );
  }
}

export default ConfirmRemoveDeviceModal;
