import React, { Component } from "react";
import { Col, FormGroup, Label } from "reactstrap";
import LayoutContainer from "../common/LayoutContainer";
import { connect } from "react-redux";
import Select from "react-select";
import { BodyComponent, SubmitButton } from "../StyledComponents";
import "antd/dist/antd.css";
import { Layout, DatePicker } from "antd";
import ResultModal from "../common/ResultModal";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { getUserCrtDevices } from "../../services/api/administration";
import {
  getSensorReport,
  getSensorReportTable,
  getSensorReportCompareTable,
} from "../../services/api/reports";
import CompareLineChart from "./CompareLineChart";
import LineChart from "./LineChart";
import jsPDF from "jspdf";
import "jspdf-autotable";
import { MdDelete } from "react-icons/md";
import CompareReports from "./CompareReports";
import LoadingComponent from "../common/LoadingComponent";
const { Content } = Layout;

const showOption = [
  { value: "compare-table", label: "Compare Table" },
  { value: "compare-graph", label: "Compare Graph" },
  { value: "table", label: "Table" },
  { value: "graph", label: "Graph" },
];

class SensorReport extends Component {
  state = {
    equipSelectedOption: null,
    showSelectedOption: null,
    startDate: null,
    endDate: null,
    showTable: false,
    showGraph: false,
    showCompareTable: false,
    showCompareGraph: false,
    showTableDownload: false,
    pageSize: 10,
    dates: [],
    datesLimit: [],
    statusFilter: "pending",
    showModal: false,
    userdata: [],
    options: [],
    loading: false,
    loadingPage: false,
    compareChartData: [],
    data: [],
    pages: 0,
  };

  graphHeader = `Sensor Report`;
  tableColumns = [
    {
      Header: `Sensor Report`,
      columns: [
        {
          accessor: "time",
          numeric: false,
          disablePadding: false,
          Header: "Time (DD/MM/YYYY HH:mm)",
          style: { whiteSpace: "unset" },
        },
        {
          accessor: "temperature",
          numeric: false,
          disablePadding: false,
          Header: "Temperature (C)",
          style: { whiteSpace: "unset" },
        },
      ],
    },
  ];

  compareTableColumns = [
    {
      accessor: "time",
      numeric: false,
      disablePadding: false,
      sortable: false,
      Header: "Time (HH:mm)",
      style: { whiteSpace: "unset" },
    },
  ];
  // Api call
  componentDidMount = () => {
    const userName = this.props.user.user_name;
    getUserCrtDevices(userName)
      .then((resp) => {
        const data = resp.data;
        data.forEach((element) => {
          let crtDevice = {
            value: element.id,
            label: `${element.id} - ${element.railway} - ${element.division} - ${element.station}`,
          };
          this.setState({
            options: [...this.state.options, crtDevice],
          });
        });
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: err.message,
        });
      });
  };
  viewSensorReport = () => {
    const crtId = this.state.equipSelectedOption;
    const startDate = Math.floor(new Date(this.state.startDate) / 1000);
    const endDate = Math.floor(new Date(this.state.endDate) / 1000);
    this.setState({
      data: [],
      compareChartData: [],
      showGraph: false,
      showTable: false,
      showCompareTable: false,
      showCompareGraph: false,
    });
    this.compareTableColumns = [
      {
        accessor: "time",
        numeric: false,
        sortable: false,
        disablePadding: false,
        Header: "Time (HH:mm)",
        style: { whiteSpace: "unset" },
      },
    ];
    if (crtId == null || this.state.showSelectedOption == null) {
      this.setState({
        showModal: true,
        modalHeader: "Wrong Entry",
        textColor: "text-danger",
        modalBody: "Please fill it properly",
      });
    } else {
      if (this.state.showSelectedOption.value === "graph") {
        if (this.state.startDate == null || this.state.endDate == null) {
          this.setState({
            showModal: true,
            modalHeader: "Wrong Entry",
            textColor: "text-danger",
            modalBody: "Please fill it properly",
          });
        } else {
          const params = {
            crtId: crtId.value,
            startDate,
            endDate,
          };
          this.setState({
            loadingPage: true,
          });
          getSensorReport(params)
            .then((resp) => {
              const data = resp.data;
              data.forEach((element) => {
                const data = {
                  x: new Date(element.time),
                  y: Number(element.temperature),
                };
                this.setState({ data: [...this.state.data, data] });
              });
            })
            .then(() => {
              this.setState({
                loadingPage: false,
                showGraph: true,
                showTable: false,
              });
            })
            .catch((err) => {
              console.log({ err }, "error while calling api");
              this.setState({
                loadingPage: false,
                showModal: true,
                modalHeader: "Error",
                textColor: "text-danger",
                modalBody: err.message,
              });
            });
        }
      }
      if (this.state.showSelectedOption.value === "table") {
        if (this.state.startDate == null || this.state.endDate == null) {
          this.setState({
            showModal: true,
            modalHeader: "Wrong Entry",
            textColor: "text-danger",
            modalBody: "Please fill it properly",
          });
        } else {
          this.fetchTableData();
          this.setState({
            showGraph: false,
            showTable: true,
          });
        }
      }
      if (this.state.showSelectedOption.value === "compare-table") {
        if (this.state.dates.length) {
          this.state.dates.forEach((element) => {
            let columns = {
              accessor: element,
              numeric: false,
              sortable: false,
              disablePadding: false,
              Header: element + " Temp (C)",
              style: { whiteSpace: "unset" },
            };
            this.compareTableColumns.push(columns);
          });
          this.setState({
            showGraph: false,
            showTable: false,
            showCompareTable: true,
          });
        } else {
          this.setState({
            showModal: true,
            modalHeader: "Wrong Entry",
            textColor: "text-danger",
            modalBody: "Please fill it properly",
          });
        }
      }
      if (this.state.showSelectedOption.value === "compare-graph") {
        if (this.state.dates.length) {
          const params = {
            crtId: crtId.value,
            dates: this.state.dates,
            datesLimit: this.state.datesLimit,
          };
          this.setState({
            loadingPage: true,
          });
          getSensorReportCompareTable(params)
            .then((resp) => {
              const data = resp.data;
              this.state.dates.forEach((datesElement) => {
                let chartDataPoint = [];
                data.forEach((dataElement) => {
                  if (dataElement[datesElement] !== undefined) {
                    let data = {
                      x: parseFloat(dataElement.time),
                      y: Number(dataElement[datesElement]),
                    };
                    chartDataPoint.push(data);
                  }
                });
                let compareChartData = {
                  name: datesElement,
                  type: "spline",
                  // yValueFormatString: "#0.## C",
                  showInLegend: true,
                  dataPoints: chartDataPoint,
                };
                this.setState({
                  compareChartData: [
                    ...this.state.compareChartData,
                    compareChartData,
                  ],
                });
              });
            })
            // .then(() => {
            //   this.state.chartDataPoint.forEach((element) => {
            //     const data = {
            //       x: new Date(element.time),
            //       y: Number(element.temperature),
            //     };
            //     this.setState({ data: [...this.state.data, data] });
            //   });
            // })
            .then(() => {
              this.setState({
                loadingPage: false,
                showGraph: false,
                showCompareTable: false,
                showTable: false,
                showCompareGraph: true,
              });
            })
            .catch((err) => {
              console.log({ err }, "error while calling api");
              this.setState({
                showModal: true,
                loadingPage: false,
                modalHeader: "Error",
                textColor: "text-danger",
                modalBody: err.message,
              });
            });
        } else {
          this.setState({
            showModal: true,
            modalHeader: "Wrong Entry",
            textColor: "text-danger",
            modalBody: "Please fill it properly",
          });
        }
      }
    }
  };
  downloadExcelSensorReport = () => {
    const crtId = this.state.equipSelectedOption;
    const startDate = Math.floor(new Date(this.state.startDate) / 1000);
    const endDate = Math.floor(new Date(this.state.endDate) / 1000);

    if (crtId == null || this.state.showSelectedOption == null) {
      this.setState({
        showModal: true,
        modalHeader: "Wrong Entry",
        textColor: "text-danger",
        modalBody: "Please fill it properly",
      });
    } else {
      if (this.state.showSelectedOption.value === "table") {
        if (this.state.startDate == null || this.state.endDate == null) {
          this.setState({
            showModal: true,
            modalHeader: "Wrong Entry",
            textColor: "text-danger",
            modalBody: "Please fill it properly",
          });
        } else {
          const params = {
            crtId: crtId.value,
            startDate,
            endDate,
          };
          getSensorReport(params)
            .then((resp) => {
              const data = resp.data;
              let csvContent = "data:text/csv;charset=utf-8,time,temperature\n";
              data.forEach((element) => {
                csvContent =
                  csvContent + `${element.time},${element.temperature}\n`;
              });
              var encodedUri = encodeURI(csvContent);
              var link = document.createElement("a");
              link.setAttribute("href", encodedUri);
              link.setAttribute("download", "sensor-report.csv");
              document.body.appendChild(link);
              link.click();
            })
            .catch((err) => {
              console.log({ err }, "error while calling api");
              this.setState({
                loadingPage: false,
                showModal: true,
                modalHeader: "Error",
                textColor: "text-danger",
                modalBody: err.message,
              });
            });
        }
      } else if (this.state.showSelectedOption.value === "compare-table") {
        if (this.state.dates.length) {
          const params = {
            crtId: crtId.value,
            dates: this.state.dates,
            datesLimit: this.state.datesLimit,
          };
          getSensorReportCompareTable(params)
            .then((resp) => {
              const data = resp.data;
              let csvContent = "data:text/csv;charset=utf-8,Time(HH: mm),";
              this.state.dates.forEach((element) => {
                csvContent = csvContent + `${element} (C),`;
              });
              csvContent = csvContent + "\n";

              data.forEach((element) => {
                csvContent = csvContent + `${element.time},`;
                this.state.dates.forEach((dateHead) => {
                  if (element[dateHead]) {
                    csvContent = csvContent + `${element[dateHead]},`;
                  } else {
                    csvContent = csvContent + ",";
                  }
                });
                csvContent = csvContent + "\n";
              });
              var encodedUri = encodeURI(csvContent);
              var link = document.createElement("a");
              link.setAttribute("href", encodedUri);
              link.setAttribute("download", "sensor-report-compare.csv");
              document.body.appendChild(link); // Required for FF
              link.click();
            })
            .catch((err) => {
              console.log({ err }, "error while calling api");
              this.setState({
                loadingPage: false,
                showModal: true,
                modalHeader: "Error",
                textColor: "text-danger",
                modalBody: err.message,
              });
            });
        } else {
          this.setState({
            showModal: true,
            modalHeader: "Wrong Entry",
            textColor: "text-danger",
            modalBody: "Please fill it properly",
          });
        }
      }
    }
  };
  downloadPdfSensorReport = () => {
    const crtId = this.state.equipSelectedOption;
    const startDate = Math.floor(new Date(this.state.startDate) / 1000);
    const endDate = Math.floor(new Date(this.state.endDate) / 1000);

    if (crtId == null || this.state.showSelectedOption == null) {
      this.setState({
        showModal: true,
        modalHeader: "Wrong Entry",
        textColor: "text-danger",
        modalBody: "Please fill it properly",
      });
    } else {
      if (this.state.showSelectedOption.value === "table") {
        if (this.state.startDate == null || this.state.endDate == null) {
          this.setState({
            showModal: true,
            modalHeader: "Wrong Entry",
            textColor: "text-danger",
            modalBody: "Please fill it properly",
          });
        } else {
          const params = {
            crtId: crtId.value,
            startDate,
            endDate,
          };
          getSensorReport(params)
            .then((resp) => {
              const data = resp.data;
              let body = [];
              data.forEach((element) => {
                body.push([`${element.time}`, `${element.temperature}`]);
              });
              var doc = new jsPDF();
              doc.autoTable({
                head: [["Time (DD/MM/YYYY HH:mm)", "Temperature (C)"]],
                body: body,
              });
              doc.save("sensor-report.pdf");
            })
            .catch((err) => {
              console.log({ err }, "error while calling api");
              this.setState({
                loadingPage: false,
                showModal: true,
                modalHeader: "Error",
                textColor: "text-danger",
                modalBody: err.message,
              });
            });
        }
      } else if (this.state.showSelectedOption.value === "compare-table") {
        if (this.state.dates.length) {
          const params = {
            crtId: crtId.value,
            dates: this.state.dates,
            datesLimit: this.state.datesLimit,
          };
          getSensorReportCompareTable(params)
            .then((resp) => {
              const data = resp.data;
              let head = [];
              let datesHead = [];
              let datesHeadShow = [];
              datesHead.push("Time(HH: mm)");
              this.state.dates.forEach((element) => {
                datesHead.push(element);
              });
              datesHeadShow.push("Time(HH: mm)");
              this.state.dates.forEach((element) => {
                datesHeadShow.push(`${element} (C)`);
              });
              head.push(datesHeadShow);
              let body = [];
              data.forEach((element) => {
                let bodyData = [];
                datesHead.forEach((dateHead) => {
                  if (dateHead === "Time(HH: mm)") {
                    bodyData.push(`${element.time}`);
                  } else {
                    if (element[dateHead]) {
                      bodyData.push(`${element[dateHead]}`);
                    } else {
                      bodyData.push("");
                    }
                  }
                });
                body.push(bodyData);
              });
              var doc = new jsPDF();
              doc.autoTable({
                head: head,
                body: body,
              });
              doc.save("sensor-report-compare.pdf");
            })
            .catch((err) => {
              console.log({ err }, "error while calling api");
              this.setState({
                loadingPage: false,
                showModal: true,
                modalHeader: "Error",
                textColor: "text-danger",
                modalBody: err.message,
              });
            });
        } else {
          this.setState({
            showModal: true,
            modalHeader: "Wrong Entry",
            textColor: "text-danger",
            modalBody: "Please fill it properly",
          });
        }
      }
    }
  };
  // Fetch Table Data
  onFetchData = (state) => {
    let orderBy, orderDir;
    if (state.sorted.length > 0) {
      orderBy = state.sorted[0].id;
      orderDir = state.sorted[0].desc ? "desc" : "asc";
    } else {
      orderBy = "time";
      orderDir = "DESC";
    }
    this.setState(
      {
        pageSize: state.pageSize,
        page: state.page,
        orderBy,
        orderDir,
      },
      this.fetchTableData
    );
  };

  fetchTableData = () => {
    this.setState({
      loading: true,
    });
    const startDate = Math.floor(new Date(this.state.startDate) / 1000);
    const endDate = Math.floor(new Date(this.state.endDate) / 1000);
    const params = {
      crtId: this.state.equipSelectedOption.value,
      startDate,
      endDate,
      rowsPerPage: this.state.pageSize,
      page: this.state.page,
      orderBy: this.state.orderBy,
      orderDir: this.state.orderDir,
    };
    // console.log(params);
    getSensorReportTable(params)
      .then((resp) => {
        this.setState({
          showTable: true,
          showGraph: false,
          loading: false,
          data: resp.data,
          pages: Math.ceil(resp.totalHits / this.state.pageSize),
        });
      })
      .catch((err) => {
        console.log({ err }, "error while calling api");
        this.setState({
          showModal: true,
          modalHeader: "Error",
          textColor: "text-danger",
          modalBody: `Error - ${err.message}`,
        });
      });
  };
  // Handle Change
  equipHandleChange = (equipSelectedOption) => {
    this.setState({
      showGraph: false,
      showTable: false,
      showCompareTable: false,
    });
    this.setState({ equipSelectedOption });
  };
  handleChangeStartDate = (e) => {
    this.setState({
      showGraph: false,
      showTable: false,
      showCompareTable: false,
    });
    this.setState({
      showGraph: false,
      showTable: false,
      showCompareTable: false,
    });
    if (e) {
      this.setState({ startDate: e._d });
    }
  };
  handleChangeEndDate = (e) => {
    this.setState({
      showGraph: false,
      showTable: false,
      showCompareTable: false,
    });
    if (e) {
      this.setState({ endDate: e._d });
    }
  };
  showHandleChange = (showSelectedOption) => {
    this.setState({
      showGraph: false,
      showTable: false,
      showTableDownload: false,
      showCompareTable: false,
    });
    this.setState({ showSelectedOption });
    if (
      showSelectedOption.value === "table" ||
      showSelectedOption.value === "graph"
    ) {
      this.setState({
        showGraphTableDate: true,
        showCompareDate: false,
      });
    }
    if (
      showSelectedOption.value === "compare-table" ||
      showSelectedOption.value === "compare-graph"
    ) {
      this.setState({
        showGraphTableDate: false,
        showCompareDate: true,
      });
    }
    if (
      showSelectedOption.value === "compare-table" ||
      showSelectedOption.value === "table"
    ) {
      this.setState({
        showTableDownload: true,
      });
    }
  };

  handleChangeSelectDates = (e, dateString) => {
    this.setState({
      showGraph: false,
      showTable: false,
      showCompareTable: false,
    });
    let i = 0;
    if (dateString) {
      this.state.dates.forEach((element) => {
        if (element !== dateString) {
          i += 1;
        }
      });
      if (i === this.state.dates.length) {
        let datesLimit = new Date(e._d);
        datesLimit = {
          upperLimit: Math.floor(datesLimit.setHours(0, 0, 0, 0) / 1000),
          lowerLimit: Math.floor(datesLimit.setHours(23, 40, 0, 0) / 1000),
        };
        this.setState({
          dates: [...this.state.dates, dateString],
          datesLimit: [...this.state.datesLimit, datesLimit],
        });
        // console.log({ datesLimit: this.state.datesLimit });
      }
    }
  };
  showAddedDates = () => {
    const selections = [];
    for (let i = 0; i < this.state.dates.length; i++) {
      selections.push(
        <FormGroup row>
          <Col sm={3}>
            <Label className="text-primary">{this.state.dates[i]}</Label>
            <MdDelete
              sm={1}
              className="icon-style text-danger"
              id={`editProductButton`}
              style={{ marginLeft: 5 }}
              onClick={() => {
                this.handleDeleteDate(i);
              }}
            />
          </Col>
        </FormGroup>
      );
    }
    return selections;
  };
  handleDeleteDate = (i) => {
    this.setState({
      showGraph: false,
      showTable: false,
      showCompareTable: false,
    });
    let dates = this.state.dates;
    let datesLimit = this.state.datesLimit;
    if (i === 0) {
      dates.splice(0, 1);
      datesLimit.splice(0, 1);
    } else {
      dates.splice(i, i);
      datesLimit.splice(i, i);
    }
    this.setState({
      dates,
      datesLimit,
    });
  };

  render() {
    return (
      <LayoutContainer>
        <Content
          className="site-layout-background"
          style={{
            padding: "36px 60px",
            background: "#F2F2F2",
          }}
        >
          <h3>
            <u>Sensor Report</u>
          </h3>
          <FormGroup row>
            <Label sm={1}>Equipment:</Label>
            <Col sm={3}>
              <Select
                value={this.state.equipSelectedOption}
                placeholder="id - railway - division - station"
                onChange={this.equipHandleChange}
                options={this.state.options}
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={1}>Show:</Label>
            <Col sm={3}>
              <Select
                value={this.state.showSelectedOption}
                onChange={this.showHandleChange}
                options={showOption}
              />
            </Col>
          </FormGroup>
          {this.state.showGraphTableDate && (
            <div>
              <FormGroup row>
                <Label sm={1}>Start date/time:</Label>
                <Col sm={3}>
                  <DatePicker
                    showTime={{ format: "HH:mm" }}
                    format="YYYY-MM-DD HH:mm"
                    onChange={this.handleChangeStartDate}
                    style={{ width: "100%" }}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Label sm={1}>End date/time:</Label>
                <Col sm={3}>
                  <DatePicker
                    showTime={{ format: "HH:mm" }}
                    format="YYYY-MM-DD HH:mm"
                    onChange={this.handleChangeEndDate}
                    style={{ width: "100%" }}
                  />
                </Col>
              </FormGroup>
            </div>
          )}
          {this.state.showCompareDate && (
            <div>
              <FormGroup row>
                <Label sm={1}>Select dates:</Label>
                <Col sm={3}>
                  <DatePicker
                    format="YYYY-MM-DD"
                    onChange={this.handleChangeSelectDates}
                    style={{ width: "100%" }}
                  />
                </Col>
              </FormGroup>
              {this.showAddedDates()}
            </div>
          )}
          <FormGroup row>
            <SubmitButton
              className="bg-primary"
              onClick={this.viewSensorReport}
            >
              View Report
            </SubmitButton>
            {this.state.showTableDownload && (
              <div>
                <SubmitButton
                  className="bg-primary"
                  onClick={this.downloadExcelSensorReport}
                >
                  Download Excel
                </SubmitButton>
                <SubmitButton
                  className="bg-primary"
                  onClick={this.downloadPdfSensorReport}
                >
                  Download Pdf
                </SubmitButton>
              </div>
            )}
          </FormGroup>
          {this.state.showTable && (
            <ReactTable
              data={this.state.data}
              columns={this.tableColumns}
              loading={this.state.loading}
              manual
              onFetchData={this.onFetchData}
              className="-striped -highlight text-center"
              pages={this.state.pages}
            />
          )}
          {this.state.showGraph && (
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                padding: "12px",
                height: "400px",
              }}
            >
              <LineChart data={this.state.data} />
            </div>
          )}
          {this.state.showCompareGraph && (
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                padding: "12px",
                height: "400px",
              }}
            >
              <CompareLineChart
                compareChartData={this.state.compareChartData}
              />
            </div>
          )}
          {this.state.showCompareTable && (
            <CompareReports
              crtId={this.state.equipSelectedOption.value}
              dates={this.state.dates}
              datesLimit={this.state.datesLimit}
              compareTableColumns={this.compareTableColumns}
            />
          )}
          {this.state.showModal && (
            <ResultModal
              modalHeader={this.state.modalHeader}
              modalBody={this.state.modalBody}
              textColor={this.state.textColor}
              closeModal={() => this.setState({ showModal: false })}
            />
          )}
          {this.state.loadingPage ? <LoadingComponent /> : null}
        </Content>
      </LayoutContainer>
    );
  }
}

const mapStateToProps = (state) => {
  const user = state.user;
  return { user };
};
export default connect(mapStateToProps)(SensorReport);
